.base-table > div {
  border: none;
  padding: 0.5rem 1.5rem;
}

.base-table > div[even]:not(.base-header) {
  background-color: #ededed;
}

.base-table > div[odd]:not(.base-header) {
  background-color: #fefefe;
}

.base-header {
  background-color: #1c8ae3;
  color: #fff;
  font-weight: 700;
  text-transform: capitalize;
}
